import { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { toggleRules, toggleRulesAccepted } from 'redux/appReducer'

export default function Rules() {
  const { showRules, rulesAccepted } = useSelector((state) => state.app)
  const dispatch = useDispatch()
  const [animation, setAnimation] = useState('hidden')
  const hideRules = () => {
    setAnimation('bounceOutDown')
    setTimeout(dispatch, 500, toggleRules())
  }
  const acceptRules = () => {
    hideRules()
    setTimeout(dispatch, 1000, toggleRulesAccepted())
  }

  useEffect(() => {
    setAnimation(showRules ? 'bounceInUp' : 'bounceOutDown')
  }, [showRules])

  return (
    <div className={`rules-wrapper animate ${animation}`}>
      <div className="rules-text white pb-3">
        <h1>Game rules</h1>
        <p>1. Accept this rules.</p>
        <p>
          2. Fill out the form with personal data.
          <br />
          <small>
            <i>(your data will not be stored or transmitted)</i>
          </small>
        </p>
        <p>
          3. Send invite to a friend.
          <br />
          <small>
            <i>(just pretend you do)</i>
          </small>
        </p>
        <p>4. Start the game and pick 3 cards.</p>
        <p>5. Have some fun!</p>
        <br />
        <p>
          <i>This game is a simple demonstration, no real money.</i>
        </p>
      </div>
      <div className="rules-controls-wrapper w-100">
        <div className="rules-controls d-flex light-blue">
          <div className="form-submit close cursor-pointer" onClick={hideRules}>
            <div className="btn pl-05 pr-05">Close</div>
          </div>
          {!rulesAccepted && (
            <div className="form-submit">
              <div className="btn active nowrap" onClick={acceptRules}>
                I accept the rules
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
